@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
* {
    box-sizing: border-box;
}

:root{
    --background: #111;
    --white: #FBFAFC;
    --green: #46D369;
    --gray: #999;
    --gray-button: #333;
    --red: #E50914;
}

body{
    background-color: #111;
    background-color: var(--background);
    margin: 0;
    color: #FBFAFC;
    color: var(--white);
    font-family: 'Roboto', sans-serif;
}

.lists{
    margin-top: -150px;
}

.loading{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}

.loading svg{
    position: relative;
    width: 150px;
    height: 150px;
    -webkit-animation: rotate 2s linear infinite;
            animation: rotate 2s linear infinite;
}

@-webkit-keyframes rotate{
    0%{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100%{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes rotate{
    0%{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100%{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.loading svg circle{
    height: 100%;
    width: 100%;
    fill: none;
    stroke-width: 10;
    stroke: #E50914;
    stroke: var(--red);
    stroke-linecap: round;
    -webkit-transform: translate(5px, 5px);
            transform: translate(5px, 5px);
    stroke-dasharray: 440;
    stroke-dashoffset: 440;
    -webkit-animation: animate 4s linear infinite;
            animation: animate 4s linear infinite;
}

@-webkit-keyframes animate{
    0%, 100%{
        stroke-dashoffset: 440;
    }
    50%{
        stroke-dashoffset: 0;
    }
    50.1%{
        stroke-dashoffset: 880;
    }
}

@keyframes animate{
    0%, 100%{
        stroke-dashoffset: 440;
    }
    50%{
        stroke-dashoffset: 0;
    }
    50.1%{
        stroke-dashoffset: 880;
    }
}
header{
    position: fixed;
    z-index: 999;
    top: 0;
    right: 0;
    left: 0;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    background: transparent;
    transition: all ease 0.5s;
}

.black-background{
    background-color: var(--background);
}

.header-logo{
    height: 25px;
}

.header-user{
    height: 35px;
}

.header-logo img{
    height: 100%;
}

.header-user img{
    height: 100%;
    border-radius: 3px;
}


.featured{
    height: 100vh;
}

.featured-horizontal-effect{
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, var(--background) 10%, transparent 90%);
}

.featured-vertical-effect{
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, var(--background) 30%, transparent 70%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-top: 70px;
    padding-bottom: 150px;
    padding-left: 30px;
}

.featured-name{
    font-size: 60px;
    font-weight: bold;
}

.featured-info{
    font-size: 18px;
    margin-top: 15px;
}

.featured-points,
.featured-year,
.featured-seasons{
    display: inline-block;
    margin-right: 15px;
    font-weight: bold;
}

.featured-points{
    color: var(--green);
}

.featured-description{
    margin-top: 15px;
    font-size: 19px;
    line-height: 130%;
    color: var(--gray);
    max-width: 40%;
}

.featured-buttons{
    margin-top: 15px;
}

.watch-button,
.mylist-button{
    display: inline-block;
    font-size: 19px;
    font-weight: bold;
    padding: 12px 25px;
    border-radius: 5px;
    text-decoration: none;
    margin-right: 30px;
    opacity: 1;
    transition: all ease 0.3s;
}

.watch-button:hover,
.mylist-button:hover{
    opacity: 0.7;
}

.watch-button{
    background-color: var(--white);
    color: var(--background);
}

.mylist-button{
    background-color: var(--gray-button);
    color: var(--white);
}

.featured-genres{
    margin-top: 15px;
    font-size: 18px;
    color: var(--gray);
}


/* MEDIA QUERIES */
@media (max-width: 760px){
    .featured{
        height: 90vh;
    }
    
    .featured-name{
        font-size: 40px;
    }

    .featured-info{
        font-size: 16px;
    }

    .featured-description{
        max-width: 100%;
        font-size: 14px;
        margin-right: 30px;
    }

    .watch-button,
    .mylist-button{
        font-size: 16px;
    }

    .featured-genres{
        font-size: 14px;
    }
}
.movieRow{
    margin-bottom: 30px;
    overflow-x: hidden;
}

.movieRow h2{
    margin: 0px 0px 0px 30px;
}

.movieRow-list{
    transition: all ease 0.4s;
}

.movieRow-item{
    display: inline-block;
    width: 150px;
    cursor: pointer;
}

.movieRow-item img{
    width: 100%;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    transition: all ease-in 0.3s;
}

.movieRow-item img:hover{
    -webkit-transform: scale(1);
            transform: scale(1);
}

.movieRow-left, 
.movieRow-right{
    position: absolute;
    width: 40px;
    height: 225px;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    opacity: 0;
    transition: all ease 0.5s;
}

.movieRow-left{
    left: 0;
}

.movieRow-right{
    right: 0;
}

.movieRow:hover .movieRow-left, 
.movieRow:hover .movieRow-right{
    opacity: 1;
}

/* MEDIA QUERIES */
@media (max-width: 760px){
    .movieRow-left, 
    .movieRow-right{
        opacity: 1;
    }
}
footer{
    margin: 50px;
    text-align: center;
}

footer a{
    text-decoration: none;
    font-size: 17px;
    color: var(--red);
    transition: all ease 0.2s;
}

footer a:hover{
    color: #EC2731;
}
