header{
    position: fixed;
    z-index: 999;
    top: 0;
    right: 0;
    left: 0;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    background: transparent;
    transition: all ease 0.5s;
}

.black-background{
    background-color: var(--background);
}

.header-logo{
    height: 25px;
}

.header-user{
    height: 35px;
}

.header-logo img{
    height: 100%;
}

.header-user img{
    height: 100%;
    border-radius: 3px;
}

